import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogActions from "@material-ui/core/DialogActions";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";
import InviteResponseSelect from "./InviteResponseSelect";
import Divider from "@material-ui/core/Divider";
import DialogParagraph from "./DialogParagraph";
import InviteResponseSelectMe from "./InvitedResponseSelectMe";
import { getDate, getTime } from "../Contactanos_Page/Contactanos_Page";

const sendEmail = () => {};

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;

  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <div>{children}</div>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function CustomizedDialogs(props) {
  const [open, setOpen] = React.useState(true);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    props.closed();
  };
  let responseMap = new Map();

  const getResponse = (data) => {
    responseMap[data.index] = data.response;
    console.log(responseMap);
  };

  const getPersonIndex = async (data) => {
    let count = 0;
    let index;
    data.rows.map((row) => {
      if (row.personId === data.personId) {
        index = count;
      }
      count = count + 1;
    });

    return index;
  };

  const messageChanged = async (data) => {
    const rows = await props.sheetThree.getRows();
    const index = await getPersonIndex({ rows: rows, personId: data.personId });
    console.log(index);
    if (index === undefined) {
      await props.sheetThree.addRow({
        personId: data.personId,
        firstName: data.firstName,
        lastName: data.lastName,
        changeDate: getDate(),
        changeCount: 1,
        changedFrom: data.changedFrom,
        changedTo: data.changedTo,
      });
    } else {
      rows[index].changeDate = getDate();
      rows[index].changeCount = parseInt(rows[index].changeCount) + 1;
      rows[index].changedFrom = data.changedFrom;
      rows[index].changedTo = data.changedTo;
      await rows[index].save();
    }
  };

  const submitRespose = () => {
    Object.keys(responseMap).forEach(async (item) => {
      let index;
      if (item.localeCompare("plusOne") === 0) {
        props.sheetRows[props.data.index].plusOneResponse = responseMap[item];
        index = props.data.index;
      } else {
        if (props.sheetRows[item].going !== "") {
          if (
            props.sheetRows[item].going.localeCompare(responseMap[item]) !== 0
          ) {
            messageChanged({
              personId: props.sheetRows[item].personId,
              firstName: props.sheetRows[item].firstName,
              lastName: props.sheetRows[item].lastName,
              changedFrom: props.sheetRows[item].going,
              changedTo: responseMap[item],
            });
          }
        }
        props.sheetRows[item].going = responseMap[item];
        index = item;
      }
      await props.sheetRows[index].save();
    });
    handleClose();
    props.submit();
  };

  return (
    <div>
      <Dialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          <DialogParagraph
            title={"Hola " + props.data.firstName + "! asistiras a la boda?"}
          />
        </DialogTitle>
        <DialogContent dividers>
          <InviteResponseSelectMe
            data={{ index: props.data.index, hasPlus: false }}
            name="Yo"
            sendResponse={getResponse.bind(this)}
          />
          {!props.data.plusOne.localeCompare("0") === false && (
            <div>
              <Divider />
              <DialogParagraph title="Tienes una invitacion para un invitado extra!" />
              <Divider />
              <InviteResponseSelect
                data={{ index: props.data.index, hasPlus: true }}
                name="Mi invitado"
                sendResponse={getResponse.bind(this)}
              />
            </div>
          )}

          {props.familyData.length !== 0 && (
            <div>
              <Divider />
              <DialogParagraph title="Tambien puedes responder por tus seres queridos" />
              {props.familyData.map((item) => (
                <div key={item.index}>
                  <Divider />
                  <InviteResponseSelect
                    data={{ index: item.index, hasPlus: false }}
                    name={item.firstName}
                    sendResponse={getResponse.bind(this)}
                  />
                </div>
              ))}
            </div>
          )}
        </DialogContent>

        <DialogActions>
          <Button onClick={submitRespose} color="primary">
            Enviar Respuesta
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
