import React from "react";

import Invited from "./Invited";
import { Component } from "react";
import CustomizedDialogs from "./DialogBox";
import { Divider } from "@material-ui/core";
import * as RB from "react-bootstrap";
import swal from "sweetalert";
import Navbar from "../Navbar";
const { GoogleSpreadsheet } = require("google-spreadsheet");
const creds = require("../client_secret.json");

const styles = {
  searchBar: {
    maxWidth: "300px",
  },
};

const doc = new GoogleSpreadsheet(
  "11tH_WVs1vmYYrm0EbE8Yx6FUqyBjl-nxF2e6Ag7Vtgo"
);

let rows;
let sheetThree;

export class RSVP_Page extends Component {
  state = {
    count: 0,
    data: [],
    search: "",
    clickedData: {},
    familyMembers: [],
    validated: false,
    updateInviteId: "",
    personData: {},
  };

  componentDidMount = async () => {
    await doc.useServiceAccountAuth({
      client_email: creds.client_email,
      private_key: creds.private_key,
    });

    await doc.loadInfo();
    const sheet = doc.sheetsByIndex[0];
    const sheet3 = doc.sheetsByIndex[2];
    rows = await sheet.getRows();
    sheetThree = await sheet3;

    this.setState({ data: rows });
  };

  updateSearch(event) {
    this.setState({ search: event.target.value });
  }

  getClickInfo = (data) => {
    this.setState({
      clickedData: data,
    });

    let familyFiltered = this.state.data.filter((row) => {
      if (row.index !== data.index) {
        return row.groupId.indexOf(data.groupId) !== -1;
      }
    });

    this.setState({ familyFiltered: familyFiltered });
  };

  closeDialog = () => {
    this.setState({ clickedData: {} });
  };

  submitted = () => {
    swal("Su respuesta ha sido recibida!", "Gracias!", "success");
  };

  updateInviteId(event) {
    this.setState({ updateInviteId: event.target.value });
  }

  handleSubmit = () => {
    const personFiltered = this.state.data.filter(
      (row) => row.personId.localeCompare(this.state.updateInviteId) === 0
    )[0];
    const familyFiltered = this.state.data.filter((row) => {
      if (row.index !== personFiltered.index) {
        return row.groupId.localeCompare(personFiltered.groupId) === 0;
      }
    });

    console.log(personFiltered);
    this.setState({
      familyMembers: familyFiltered,
      clickedData: personFiltered,
    });
  };

  formSubmit = (event) => {
    event.preventDefault();
    event.stopPropagation();
  };

  isButtonDisabled = () => {
    return this.state.data.filter(
      (row) => row.personId.localeCompare(this.state.updateInviteId) === 0
    ).length > 0
      ? false
      : true;
  };

  render() {
    let dataFiltered = this.state.data.filter((row) => {
      let name = row.firstName.toLowerCase() + " " + row.lastName.toLowerCase();
      let nameReverse =
        row.lastName.toLowerCase() + " " + row.firstName.toLowerCase();
      return (
        (name.toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1) +
        (nameReverse.toLowerCase().indexOf(this.state.search.toLowerCase()) !==
          -1)
      );
    });

    return (
      <div className="rsvp-container">
        <div className="wrapper container ">
          {this.state.data.length === 0 && (
            <div
              style={{
                position: "fixed",
                zIndex: "1031",
                top: "50%",
                left: "50%",
                transform: "translate(-50%,-50%)",
              }}
            >
              <div
                className="spinner-border text-success"
                style={{ width: "3rem", height: "3rem" }}
                role="status"
              >
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          )}

          <div className="row align-items-center justify-content-center">
            {this.state.data.length !== 0 && (
              <div className="col-7 col-lg-3 col-sm-6 col-md-5 shadow-sm p-1 mb-5 bg-white rounded p-4">
                <RB.Form id="contactForm" noValidate onSubmit={this.formSubmit}>
                  <RB.Form.Group controlId="formBasicPassword">
                    <RB.Form.Label>
                      Escriba su numero de invitacion
                    </RB.Form.Label>
                    <RB.Form.Control
                      type="number"
                      placeholder="Numero de invitacion"
                      onChange={this.updateInviteId.bind(this)}
                    />
                  </RB.Form.Group>
                  <RB.Button
                    disabled={this.isButtonDisabled()}
                    variant="primary"
                    type="submit"
                    onClick={this.handleSubmit}
                  >
                    Submit
                  </RB.Button>
                </RB.Form>
              </div>
            )}

            {Object.keys(this.state.clickedData).length !== 0 && (
              <CustomizedDialogs
                data={this.state.clickedData}
                familyData={this.state.familyMembers}
                closed={this.closeDialog.bind(this)}
                submit={this.submitted.bind(this)}
                sheetRows={rows}
                sheetThree={sheetThree}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default RSVP_Page;
