import React, { Component } from "react";
import DashboardGrid from "./DashboardGrid";
import PieChart from "./Charts/PieChart";
import GaugeChart from "./Charts/GaugeChart";
import { columnDef } from "./ColumnDef";
import CustomizedDialogs from "./DialogBox";
import { columnDefMessages, columnDefResponses } from "./ColumnDef";

const { GoogleSpreadsheet } = require("google-spreadsheet");
const creds = require("../client_secret.json");
const doc = new GoogleSpreadsheet(
  "11tH_WVs1vmYYrm0EbE8Yx6FUqyBjl-nxF2e6Ag7Vtgo"
);

let rows1;
let rows2;
let rows3;

class Dashboard extends Component {
  state = {
    columnDefs: columnDef,
    rowData: [],
    rowData2: [],
    rowData3: [],
    pieData: [],
    dialogRowData: [],
    dialogColumnData: [],
    dialogOpen: false,
  };

  componentDidMount = async () => {
    await doc.useServiceAccountAuth({
      client_email: creds.client_email,
      private_key: creds.private_key,
    });

    await doc.loadInfo();
    const sheet1 = doc.sheetsByIndex[0];
    const sheet2 = doc.sheetsByIndex[1];
    const sheet3 = doc.sheetsByIndex[2];
    rows1 = await sheet1.getRows();
    rows2 = await sheet2.getRows();
    rows3 = await sheet3.getRows();
    this.setState({ rowData: rows1, rowData2: rows2, rowData3: rows3 }, () =>
      this.setPieData()
    );
  };

  setPieData = () => {
    const data = [
      {
        name: "Going",
        y: this.filterResponses(0).length,
      },
      {
        name: "Not Going",
        y: this.filterResponses(1).length,
      },
      {
        name: "Not Sure Yet",
        y: this.filterResponses(2).length,
      },
      {
        name: "No Response",
        y: this.filterResponses(4).length,
      },
    ];
    this.setState({ pieData: data });
  };

  handleDialogClose = (type) => {
    this.setState({ dialogOpen: false });
  };

  handleDialogOpen = (type) => {
    if (type === "m") {
      this.setState({
        dialogColumnData: columnDefMessages,
        dialogRowData: this.state.rowData2,
      });
    } else {
      this.setState({
        dialogColumnData: columnDefResponses,
        dialogRowData: this.state.rowData3,
      });
    }
    this.setState({ dialogOpen: true });
  };

  filterResponses = (type) => {
    switch (type) {
      case 0:
        return this.state.rowData.filter(
          (row) => row.going.localeCompare("going") === 0
        );
      case 1:
        return this.state.rowData.filter(
          (row) => row.going.localeCompare("not going") === 0
        );
      case 2:
        return this.state.rowData.filter(
          (row) => row.going.localeCompare("not sure yet") === 0
        );
      default:
        return this.state.rowData.filter(
          (row) => row.going.localeCompare("") === 0
        );
    }
  };

  render() {
    return (
      <div style={{ backgroundColor: "#FBFBFB" }}>
        {this.state.rowData.length !== 0 ? (
          <div
            className="container-fluid"
            style={{
              backgroundColor: "#FBFBFB",
              paddingBottom: "70px",
              paddingTop: "20px",
              width: "90%",
            }}
          >
            <div className="row align-items-center justify-content-center shadow p-1 mb-3 bg-white rounded pt-4">
              <div className="col-sm ">
                {this.state.pieData.length !== 0 && (
                  <PieChart pieData={this.state.pieData} />
                )}
              </div>
              {this.state.dialogOpen && (
                <CustomizedDialogs
                  open={this.state.dialogOpen}
                  rowData={this.state.dialogRowData}
                  close={this.handleDialogClose}
                  columnDefs={this.state.dialogColumnData}
                />
              )}

              <div className="col-sm ">
                <div className="row">
                  <div className="col-sm text-center">
                    <div
                      style={{
                        color: "#28B463",
                        fontSize: "120px",
                        fontWeight: "600",
                      }}
                    >
                      {this.state.rowData2.length}
                    </div>
                    <button
                      className="btn bg-transparent"
                      style={{ color: "#4d514d", fontSize: "30px" }}
                      onClick={() => this.handleDialogOpen("m")}
                    >
                      Messages
                    </button>
                  </div>
                  <div className="col-sm text-center">
                    <div
                      style={{
                        color: "#2980B9",
                        fontSize: "120px",
                        fontWeight: "600",
                      }}
                    >
                      {this.state.rowData3.length}
                    </div>
                    <button
                      className="btn bg-transparent"
                      style={{ color: "#4d514d", fontSize: "30px" }}
                      onClick={() => this.handleDialogOpen("r")}
                    >
                      Response(s) Changed
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div className="row shadow p-1 mb-3 bg-white rounded pt-5 pb-4 mt-5">
              <div className="col-sm">
                <div className="mb-4">
                  <h3 style={{ color: "#4d514d", fontWeight: "600" }}>
                    Invitation Responses
                  </h3>
                </div>
                <DashboardGrid
                  rowData={this.state.rowData}
                  columnDefs={this.state.columnDefs}
                />
              </div>
            </div>
          </div>
        ) : (
          <div
            style={{
              position: "fixed",
              zIndex: "1031",
              top: "50%",
              left: "50%",
              transform: "translate(-50%,-50%)",
            }}
          >
            <div
              className="spinner-border text-success"
              style={{ width: "3rem", height: "3rem" }}
              role="status"
            >
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default Dashboard;
