import colors from "./UIColors";

const responseValueGetter = (params, type) => {
  let value;
  if (type === 0) {
    value = params.data.going;
  }
  if (type === 1) {
    value = params.data.changedFrom;
  }
  if (type === 2) {
    value = params.data.changedTo;
  }
  switch (value) {
    case "going":
      return "GOING";
    case "not going":
      return "NOT GOING";
    case "not sure yet":
      return "NOT SURE YET";
    default:
      return "NO ANSWER";
  }
};

const plusOneValueGetter = (params) => {
  if (params.data.plusOne.localeCompare("1") !== 0) {
    return "";
  }
  switch (params.data.plusOneResponse) {
    case "going":
      return "GOING";
    case "not going":
      return "NOT GOING";
    case "not sure yet":
      return "NOT SURE YET";
    case undefined:
      if (params.data.plusOne.localeCompare("1") === 0) {
        return "NO RESPONSE";
      } else {
        return "";
      }

    default:
      return "";
  }
};

const responseCellStyle = (params, type) => {
  let value;
  if (type === 0) {
    value = params.data.going;
  }
  if (type === 1) {
    value = params.data.changedFrom;
  }
  if (type === 2) {
    value = params.data.changedTo;
  }
  switch (value) {
    case "going":
      return { color: colors.green, fontWeight: "700" };
    case "not going":
      return { color: colors.red, fontWeight: "700" };
    case "not sure yet":
      return { color: colors.blue, fontWeight: "700" };
    default:
      return { color: colors.orange, fontWeight: "700" };
  }
};

const plusOneCellStyle = (params) => {
  switch (params.data.plusOneResponse) {
    case "going":
      return { color: colors.green, fontWeight: "700" };
    case "not going":
      return { color: colors.red, fontWeight: "700" };
    case "not sure yet":
      return { color: colors.blue, fontWeight: "700" };
    case undefined:
      if (params.data.plusOne.localeCompare("1") === 0) {
        return { color: colors.orange, fontWeight: "700" };
      }
  }
};

export const columnDef = [
  { headerName: "Family ID", field: "groupId", sort: "desc" },
  { headerName: "First Name", field: "firstName" },
  { headerName: "Last Name", field: "lastName" },
  { headerName: "Email", field: "email" },
  {
    headerName: "Response",
    field: "going",
    valueGetter: (params) => responseValueGetter(params, 0),
    cellStyle: (params) => responseCellStyle(params, 0),
  },
  {
    headerName: "Plus One Response",
    field: "Plus One Response",
    valueGetter: plusOneValueGetter,
    cellStyle: plusOneCellStyle,
  },
];

