import React, { Component } from "react";
import * as RB from "react-bootstrap";
import swal from "sweetalert";
import Reception from "../images/Ceremonia/Reception-Test.jpeg";

const { GoogleSpreadsheet } = require("google-spreadsheet");
const creds = require("../client_secret.json");

var today = new Date();

const doc = new GoogleSpreadsheet(
  "11tH_WVs1vmYYrm0EbE8Yx6FUqyBjl-nxF2e6Ag7Vtgo"
);

export const getDate = () => {
  const date =
    today.getMonth() + 1 + "/" + today.getDate() + "/" + today.getFullYear();
  return date;
};

export const getTime = () => {
  const time = today.getHours() + ":" + today.getMinutes();
  return time;
};

const mountData = async () => {
  await doc.useServiceAccountAuth({
    client_email: creds.client_email,
    private_key: creds.private_key,
  });

  await doc.loadInfo();
  const sheet = doc.sheetsByIndex[1];

  return sheet;
};

const submitMessage = async (props) => {
  const sheet = await mountData();
  await sheet.addRow({
    date: getDate(),
    time: getTime(),
    name: props.name,
    email: props.email,
    message: props.message,
  });
};

const initialState = {
  email: "",
  name: "",
  message: "",
  validated: false,
};

class Contactanos extends Component {
  state = initialState;
  //   componentDidMount = async () => {
  //     submitMessage({
  //       name: "machis",
  //       email: "mauriciobarbier@gmail.com",
  //       message: "No puedo picarle a que si ire",
  //     });
  //   };
  updateEmail = (event) => {
    this.setState({ email: event.target.value });
  };

  updateName = (event) => {
    this.setState({ name: event.target.value });
  };

  updateMessage = (event) => {
    this.setState({ message: event.target.value });
  };

  submit = (event) => {
    submitMessage({
      email: this.state.email,
      name: this.state.name,
      message: this.state.message,
    });
    event.preventDefault();
    event.stopPropagation();
    document.getElementById("contactForm").reset();
    this.setState(initialState);
  };

  handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
      this.setState({ validated: true });
    }

    if (form.checkValidity() === true) {
      this.submit(event);
      swal("Su mensaje ha sido enviado!", "Gracias!", "success");
    }
  };

  render() {
    return (
      <div
        className="jumbotron  background-pic-contact "
        style={{ height: "110vh", overflow: "auto" }}
      >
        <div className="container h-100">
          <div className="row h-100 justify-content-center align-items-center">
            <div className="col-lg-5 col-sm-8 col-md-6 shadow-sm p-1 mb-5 bg-white rounded p-4">
              <RB.Form
                id="contactForm"
                noValidate
                validated={this.state.validated}
                onSubmit={this.handleSubmit}
              >
                <RB.Form.Group controlId="formBasicEmail">
                  <RB.Form.Label>Correo de contacto</RB.Form.Label>
                  <RB.Form.Control
                    type="email"
                    placeholder="Escriba su correo"
                    onChange={this.updateEmail.bind(this)}
                    required
                  />
                  <RB.Form.Control.Feedback type="invalid">
                    Porfavor escriba un correo.
                  </RB.Form.Control.Feedback>
                </RB.Form.Group>

                <RB.Form.Group controlId="formBasicPassword">
                  <RB.Form.Label>Nombre</RB.Form.Label>
                  <RB.Form.Control
                    type="name"
                    placeholder="Escriba su nombre"
                    onChange={this.updateName.bind(this)}
                    required
                  />
                  <RB.Form.Control.Feedback type="invalid">
                    Porfavor escriba su nombre
                  </RB.Form.Control.Feedback>
                </RB.Form.Group>
                <RB.Form.Group>
                  <RB.Form.Label>Escriba su mensaje aqui</RB.Form.Label>
                  <RB.Form.Control
                    as="textarea"
                    rows="3"
                    required
                    onChange={this.updateMessage.bind(this)}
                  />
                  <RB.Form.Control.Feedback type="invalid">
                    Porfavor escriba un mensaje
                  </RB.Form.Control.Feedback>
                </RB.Form.Group>
                <RB.Button variant="primary" type="submit">
                  Submit
                </RB.Button>
              </RB.Form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contactanos;
