import React from "react";

import {
  HashRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

import { Root } from "./Root";
import RSVP_Page from "./RSVP_Page/RSVP_Page";
import Ceremonia from "./Ceremonia_Page/Ceremonia_Page";
import Contactanos from "./Contactanos_Page/Contactanos_Page";
import Dashboard from "./Dashboard/Dashboard";

import { Component } from "react";

class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <Switch>
            <Root>
              {/* <Redirect exact from="/" to="/RSVP" /> */}
              <Route exact path={"/"}>
                <RSVP_Page />
              </Route>
              <Route path={"/Ceremonia"}>
                <Ceremonia />
              </Route>
              <Route path={"/Contactanos"}>
                <Contactanos />
              </Route>
              <Route path={"/Dashboard"}>
                <Dashboard />
              </Route>
            </Root>
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
